body {
  background: #FFF;
  font-family: "Proxima Nova", "Helvetica", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  color: #1F1F1F;
  scrollbar-gutter: stable;
}
body.no-scroll {
  overflow: hidden;
  scrollbar-gutter: stable;
}

@media screen and (min-width: 768px) {
  body {
    font-size: 16px;
    line-height: 140%;
  }
}
@media screen and (min-width: 1800px) {
  body {
    font-size: 18px;
    line-height: 144%;
  }
}
a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

picture {
  display: block;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.swiper-button-prev, .swiper-button-next {
  width: 48px;
  height: 48px;
  padding: 6px;
  background: var(--boxBg);
  border-radius: 3px;
  pointer-events: auto;
  display: none;
  transition: var(--transition);
}
.swiper-button-prev::after, .swiper-button-next::after {
  color: #1F1F1F;
  font-size: 20px;
}
.swiper-button-prev:hover, .swiper-button-prev:focus, .swiper-button-next:hover, .swiper-button-next:focus {
  background: var(--accent);
}
.swiper-button-prev:hover::after, .swiper-button-prev:focus::after, .swiper-button-next:hover::after, .swiper-button-next:focus::after {
  color: #fff;
}
.swiper-button-prev:active, .swiper-button-next:active {
  background: var(--thColor);
}
.swiper-button-prev:active::after, .swiper-button-next:active::after {
  color: #fff;
}
.swiper-button-prev {
  left: 10px;
}
.swiper-button-next {
  right: 10px;
}

@media screen and (min-width: 1280px) {
  .swiper-button-prev, .swiper-button-next {
    display: block;
  }
}
.wrapper {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.container {
  width: 100%;
  max-width: var(--containerWidth);
  margin: 0 auto;
  padding: 0 var(--mainPadding);
}

.main-title {
  margin-bottom: var(--mainMargin);
  font-weight: 800;
  font-size: 24px;
  line-height: 100%;
}

.title {
  font-weight: 700;
  font-size: 22px;
  line-height: 120%;
}
.title--special {
  margin-bottom: 20px;
  padding-bottom: 23px;
  border-bottom: 3px solid var(--accent);
  line-height: 100%;
}
.title--subtitle {
  margin-bottom: 20px;
  font-size: 18px;
}

.btn {
  padding: 9px 19px;
  font-weight: 600;
  font-size: 14px;
  line-height: 100%;
  text-align: center;
  color: #fff;
  background: var(--accent);
  border-radius: 3px;
  border: 1px solid transparent;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: var(--transition);
}
.btn:hover, .btn:focus {
  background: #B22414;
}
.btn:active {
  background: #942114;
}
.btn--sec {
  background: #EAEAEA;
  color: #1F1F1F;
}
.btn--sec:hover, .btn--sec:focus {
  background: #D8D8D8;
  color: #1F1F1F;
}
.btn--sec:active {
  background: var(--thColor);
  color: #fff;
}
.btn--third {
  background: var(--thColor);
  color: #fff;
}
.btn--third:hover, .btn--third:focus {
  background: #4B4B4B;
}
.btn--third:active {
  background: var(--thColor);
}
.btn--other {
  background: #fff;
  color: #1F1F1F;
  border: 1px solid var(--textSec);
}
.btn--other:hover, .btn--other:focus {
  background: #1F1F1F;
  border-color: #1F1F1F;
  color: #fff;
}
.btn--other:active {
  opacity: 0.5;
}
.btn--wide {
  min-height: 48px;
  font-size: 16px;
}
.btn--icon {
  width: 36px;
  min-width: 36px;
  height: 36px;
  min-height: 36px;
  padding: 5px;
}
.btn--style {
  background: var(--boxBg);
}
.btn--style:hover, .btn--style:focus {
  background: #D8D8D8;
  color: #1F1F1F;
}
.btn--style:active {
  background: var(--thColor);
  color: #fff;
}
.btn svg {
  width: 23px;
  height: 23px;
}

.list {
  font-size: 12px;
  line-height: 130%;
}
.list li {
  padding-left: 31px;
  margin-bottom: 15px;
  position: relative;
}
.list li::before {
  content: "";
  position: absolute;
  left: 0;
  top: -2px;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  border: 3px solid #D82916;
}

.listing li {
  margin-bottom: 6px;
  padding-left: 18px;
  position: relative;
}
.listing li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 5px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #D9D9D9;
}

.text p {
  margin-bottom: 17px;
}
.text strong {
  font-weight: 700;
}

.num-list {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 120%;
  list-style-type: none;
  counter-reset: list1;
}
.num-list li {
  margin-bottom: 10px;
  padding-left: 20px;
  position: relative;
  list-style-type: none;
}
.num-list li:last-child {
  margin-bottom: 0;
}
.num-list li::before {
  font-weight: 500;
  position: absolute;
  top: 0;
  left: 0;
  width: 22px;
  text-align: left;
  color: #D82816;
  font-size: 16px;
  counter-increment: list1;
  content: counter(list1) ".";
}
.num-list ol {
  margin-top: 10px;
  padding: 0;
  padding-left: 10px;
  counter-reset: list2;
}
.num-list ol li {
  padding-left: 32px;
  margin-bottom: 15px;
}
.num-list ol li::before {
  width: 32px;
  counter-increment: list2;
  content: counter(list1) "." counter(list2) ".";
}

.dot-list {
  margin-bottom: 20px;
  padding-left: 20px;
  padding-right: 10px;
  font-weight: 400;
  font-size: 13px;
  line-height: 138%;
}
.dot-list li {
  margin-bottom: 10px;
  padding-left: 20px;
  position: relative;
}
.dot-list li::before {
  content: "";
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--accent);
  position: absolute;
  left: 0;
  top: 5px;
}

.sub-title {
  margin-bottom: 14px;
  font-weight: 700;
  font-size: 14px;
  line-height: 125%;
}

@media screen and (min-width: 768px) {
  .title {
    font-size: 24px;
  }
  .title--special {
    margin-bottom: 40px;
    padding-bottom: 20px;
  }
  .title--subtitle {
    margin-bottom: 30px;
    font-size: 20px;
  }

  .main-title {
    font-size: 40px;
  }

  .btn {
    min-height: 48px;
