    font-size: 16px;
  }
  .btn--icon {
    width: 36px;
    min-width: 36px;
    height: 36px;
    min-height: 36px;
    padding: 6px;
  }

  .list {
    font-size: 14px;
  }
  .list li {
    padding-left: 37px;
  }

  .text p {
    margin-bottom: 21px;
  }

  .listing li {
    margin-bottom: 8px;
  }
}
@media screen and (min-width: 1280px) {
  .main-title {
    font-size: 46px;
  }
}
@media screen and (min-width: 1366px) {
  .title {
