@import "./normalize"
@import "./vars"
@import "./mixins"
@import "./fonts"
@import "./sprite_generated"

// From libs:
//@import "../libs/LIB_NAME/path/to/style.scss"
@import "../libs/swiper/swiper-bundle.min.css"
@import "../libs/magnific-popup/dist/magnific-popup.css"

html
   box-sizing: border-box
   scrollbar-gutter: stable

*,
*::before,
*::after
   box-sizing: border-box
    // min-height: 0.001vw //  Fixes Safari bug with viewport units in calc()

.content-box-component
   box-sizing: content-box

body
   background: #FFF
   font-family: 'Proxima Nova', 'Helvetica', Arial, sans-serif
   width: 100%
   height: 100%
   min-height: 100vh
   -webkit-font-smoothing: antialiased
   -moz-osx-font-smoothing: grayscale
   font-weight: 400
   font-size: 14px
   line-height: 129%
   color: #1F1F1F
   scrollbar-gutter: stable
   &.no-scroll
      overflow: hidden
      scrollbar-gutter: stable

@media screen and (min-width: $width_tab)
   body
      font-size: 16px
      line-height: 140%

@media screen and (min-width: $width_huge)
   body
      font-size: 18px
      line-height: 144%

a
   text-decoration: none
   color: inherit

button
   padding: 0
   cursor: pointer
   background: transparent
   border: none
   color: inherit

ul, ol
   margin: 0
   padding: 0

ul
   list-style: none

p
   margin: 0

h1,
h2,
h3,
h4,
h5,
h6
   margin: 0

img
   display: block
   max-width: 100%
   height: auto

picture
   display: block

svg
   max-width: 100%
   max-height: 100%

.visually-hidden:not(:focus):not(:active)
   position: absolute
   width: 1px
   height: 1px
   margin: -1px
   border: 0
   padding: 0
   clip: rect(0 0 0 0)
   overflow: hidden

.background-image
   position: absolute
   top: 0
   bottom: 0
   left: 0
   right: 0
   overflow: hidden
   picture
      position: absolute
      top: 0
      bottom: 0
      left: 0
      right: 0
   img
      width: 100%
      height: 100%
      object-fit: cover
      object-position: center